<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>
    <router-link to="./city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'homeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    display flex
    line-height $headerHeight
    background $bgColor
    color #ffffff
    .header-left
      width .64rem
      float left
      .back-icon
        text-align center
        font-size .3rem
    .header-input
      flex 1
      height .64rem
      line-height .64rem
      margin .12rem 0 .12rem .2rem
      padding-left .2rem
      background #ffffff
      border-radius .1rem
      color #cccccc
    .header-right
      min-width 1.04rem
      float right
      text-align center
      color #ffffff
      padding 0 0.1rem
      .arrow-icon
        font-size .24rem
</style>



