<template>
  <div>
    <router-link
      class="header-abs"
      tag="div"
      to="/"
      v-show="showAbs"
    >
      <div class="iconfont header-abs-back">&#xe624;</div>
    </router-link>
    <header-fixed
      v-show="!showAbs"
      :style="opacityStyle"
      title="景点详情"
    >
    </header-fixed>
  </div>
</template>

<script>
import HeaderFixed from 'common/headerfixed/HeaderFixed'
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: 0
    }
  },
  components: {
    HeaderFixed
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
      if (top > 60) {
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = {opacity}
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    // 解绑全局事件
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-abs
    position  absolute
    left 0.2rem
    top 0.2rem
    width 0.8rem
    height 0.8rem
    line-height 0.8rem
    border-radius: 0.4rem
    text-align center
    background rgba(0,0,0,0.2)
    .header-abs-back
      color #ffffff
      font-size: 0.3rem
</style>



