<template>
  <div>
    <span class="item-title-icon" v-show="!showIcon"></span>
    <div class="item border-bottom"
         v-for="(item, index) of list"
         :key="index"
    >
      <div class="item-title">
        {{item.title}}
      </div>
      <div class="item-price" v-if="item.price">
        ￥{{item.price}}
        <span class="item-desc">起</span>
      </div>
      <div class="item-children" v-if="item.children">
        <span class="item-title-icon" v-show="showIcon"></span>
        <detail-list :list="item.children">
        </detail-list>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    list: Array
  },
  data () {
    return {
      showIcon: true
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .item
    position relative
  .item-title-icon
    position: absolute
    left: .1rem
    top: .25rem
    display: inline-block
    width: .36rem
    height: .36rem
    background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
    margin-right: .1rem
    background-size: .4rem 3rem
  .item-title
    max-width 65%
    display inline-block
    line-height 0.8rem
    font-size 0.32rem
    padding 0 0rem 0rem 0.5rem
    ellipsis()
  .item-price
    float right
    color #ff3300
    font-size 0.4rem
    padding 0.2rem 0
    .item-desc
      color #999999
      font-size 0.24rem
      margin-left -0.1rem
  .item-children
    padding 0 0.2rem
    background #FFF8E2
    color #aaaaaa
</style>



