<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwier" ref="mySwiper">
      <swiper-slide v-for="item of swiperList" :key="item.id">
        <img class="swiper-img" :src="item.imgUrl" />
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
export default {
  name: 'HomeSwiper',
  props: {
    swiperList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        loop: true,
        autoplay: 1500,
        autoplayDisableOnInteraction: false,
        effect: 'fade',
        fade: {
          crossFade: false
        }
      }
    }
  },
  computed: {
    showSwier () {
      return this.swiperList.length
    }
  }
}
</script>
<style lang="stylus" scoped>
  .wrapper >>> .swiper-pagination-bullet-active
    background #ffffff !important
  .wrapper
    width 100%
    overflow hidden
    height 0
    padding-bottom 36.96%
    background #eeeeee
    .swiper-img
      width 100%
</style>



