<template>
  <div>
    <div class="header-fixed">
      <router-link to="/">
        <div class="iconfont header-fixed-back">&#xe624;</div>
      </router-link>
      {{this.title}}
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HeaderFixed',
  computed: {
    ...mapState(['title'])
  }

}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-fixed
    position fixed
    top 0
    right 0
    left 0
    height $headerHeight
    line-height $headerHeight
    text-align center
    color #ffffff
    background $bgColor
    font-size 0.32rem
    z-index 99
    .header-fixed-back
      position absolute
      width 0.64rem
      text-align center
      font-size 0.32rem
      top 0
      left 0
      color #ffffff
</style>



