<template>
  <div>
    <div class="title">周末去哪儿</div>
    <ul>
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="(item, index) of weekendList"
        :key="index"
        :to="'/weekendDetail/' + item.id"
        >
        <div class="item-img-wrapper" @click="handleItemClick(item.title)">
          <img class="item-img" :src="item.imgUrl">
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </router-link>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: Array
  },
  methods: {
    handleItemClick (title) {
      this.$store.commit('changeTitle', title)
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .border-bottom
    &:before
      border-bottom .3rem solid #f3f3f3
  .title
    line-height .8rem
    background #eeeeee
    text-indent .2rem
  .item-img-wrapper
    overflow hidden
    height 0
    padding-bottom 37.09%
    .item-img
      width 100%
  .item-info
    padding .2rem .2rem .3rem .2rem
    .item-title
      line-height .54rem
      font-size .32rem
      ellipsis()
    .item-desc
      line-height .4rem
      color #cccccc
      ellipsis()
</style>



