<template>
  <div>
    <div class="title">热门景点</div>
    <ul>
        <router-link
          tag="li"
          class="item border-bottom"
          v-for="item of recommendList"
          :key="item.id"
          :to="'/detail/' + item.id"
        >
          <div>
            <img class="item-img" :src="item.imgUrl">
          </div>
          <div class="item-info">
            <p class="item-title">{{item.title}}</p>
            <p class="item-desc">{{item.desc}}</p>
            <div class="item-ptice-info">
              <span class="item-price-icon">￥</span>
              <span class="item-price">
              {{item.price}}
              </span>
            </div>
          </div>
          <div class="item-button-wrapper">
            <div class="item-button">查看详情</div>
          </div>
      </router-link>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'HomeRecommend',
  props: {
    recommendList: Array
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    line-height .8rem
    background #eeeeee
    text-indent .2rem
  .item
    overflow hidden
    display flex
    height 1.9rem
    .item-img
      width 2.24rem
      height 1.7rem
      padding .1rem
    .item-info
      flex: 1
      padding .1rem
      min-width 0
      .item-title
        line-height .54rem
        font-size .32rem
        ellipsis()
      .item-desc
        margin-top .1rem
        line-height .4rem
        color #aaaaaa
        ellipsis()
      .item-ptice-info
        padding-top .16rem
        bottom 0
        .item-price
          line-height .4rem
          font-size .4rem
          margin-left -0.1rem
          color #ff3300
        .item-price-icon
          line-height .2rem
          font-size .24rem
          color #ff3300
    .item-button
      line-height: .5rem
      margin: .7rem .2rem
      background: #ff9300
      padding: 0 .2rem
      border-radius: .06rem
      color: #fff
</style>



