<template>
  <div class="scroller border-top">
    <div class="header">
      <h2>{{title}}</h2>
      <a href="#">全部榜单</a>
    </div>
    <div class="content">
      <ul class="all-item">
        <li class="item-content" v-for="item in list" :key="item.id">
          <img class="item-img" :src="item.imgUrl">
          <div class="item-desc">
            <span class="title">{{item.desc}}</span>
            <span class="price-color">
              ￥{{item.price}}
              <span class="price-text">起</span>
            </span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeScroller',
  props: {
    list: Array
  },
  data () {
    return {
      title: '本周热门榜单'
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .border-top
    &:before
      border-bottom 0.3rem solid #f3f3f3
  .scroller
    // padding: 0.2rem
    margin-top 0.2rem
    // background #f3f3f3
    .header
      height 0.52rem
      line-height 0.52rem
      padding 0.3rem 0.2rem
      h2
        display inline-block
      a
        float right
        &:last-child
          color $bgColor
    .all-item
      overflow-x: auto;
      white-space: nowrap;
      text-align center
      .item-content
        display inline-block
        width 2rem
        margin-left 0.07rem
        &:first-child
          padding-left 0.2rem
        .item-img
          height 2rem
        .item-desc
          padding 0.2rem
          .title
            display block
            max-width 100%
            font-size 0.24rem
            ellipsis()
          .price-color
            font-size 0.24rem
            color #ff3300
            .price-text
              font-size 0.24rem
              color black
</style>



