<template>
  <div>
    <div class="search">
      <input
        class="search-input"
        type="text"
        placeholder="输入城市名或拼音"
        v-model="keyword"
      >
    </div>
    <div
      class="search-content"
      ref="search"
      v-show="keyword"
    >
      <ul>
        <li
          class="search-item border-bottom"
          v-for="item of list"
          :key="item.id"
          @click="handleCityClick(item.name)"
        >
          {{item.name}}
        </li>
        <li class="search-item border-bottom" v-show="hasNoData">
          没有找到匹配数据
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Bscroll from 'better-scroll'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      list: [],
      timer: null
    }
  },
  methods: {
    handleCityClick (city) {
      // console.log(city)
      this.keyword = ''
      this.$store.commit('changeCity', city)
      this.$router.push('/')
    }
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          // 对键值对进行遍历
          this.cities[i].forEach((value) => {
            if (value.spell.indexOf(this.keyword) > -1 || value.name.indexOf(this.keyword) > -1) {
              result.push(value)
            }
          })
        }
        this.list = result
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.search, {
      click: true
    })
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .search
    height 0.72rem
    padding 0 0.1rem
    background $bgColor
    .search-input
      box-sizing border-box
      width 100%
      height 0.62rem
      padding 0 0.1rem
      line-height 0.62rem
      text-align center
      border-radius: 0.06rem
      color #aaaaaa
    input::-webkit-input-placeholder
      color #aaaaaa
    input:-moz-placeholder
      color #aaaaaa
    input::-moz-placeholder
      color #aaaaaa
    input:-ms-input-placeholder
      color #aaaaaa
    input:focus::-webkit-input-placeholder
      color: transparent
    input:focus:-moz-placeholder
      color: transparent
    input:focus::-moz-placeholder
      color: transparent
    input:focus:-ms-input-placeholder
      color: transparent
  .search-content
    overflow hidden
    position  absolute
    top 1.58rem
    left 0
    right 0
    bottom 0
    background #eeeeee
    z-index 1
    .search-item
      line-height 0.62rem
      padding-left 0.2rem
      background #ffffff
      color #aaaaaa
</style>



