<template>
  <div>
    <header-fixed ></header-fixed>
    <home-weekend :weekendList="weekendDetail"></home-weekend>
  </div>
</template>
<script>
import HeaderFixed from 'common/headerfixed/HeaderFixed'
import HomeWeekend from 'common/weekend/Weekend'
import axios from 'axios'
export default {
  name: 'WeekendDetail',
  components: {
    HeaderFixed,
    HomeWeekend
  },
  data () {
    return {
      weekendDetail: []
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('/gowhere/static/mock/weekend.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.handleGetDataSucc)
    },
    handleGetDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.weekendDetail = data.weekendDetail
      }
    }
  },
  mounted () {
    this.getDetailInfo()
  }
}
</script>

<style lang="stylus" scoped>

</style>



